import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
      likes: Math.floor(Math.random() * 100) + 10,
      liked: false,
      // count: 0,
    }
  },
  mutations: {
    changeName(state) {
      state.name = 'Ahn'
    },
    plusAge(state, data) {
      state.age += data
    },
    like(state) {
      if(state.liked == false) {
        state.likes++;
        state.liked = true;
      } else {
        state.likes--;
        state.liked = false;
      }
    },
    // randomLikes(state) {
    //   let randL = Math.floor(Math.random() * 100) + 10;
    //   state.likes = randL;
    // },

    // publish(state) {
    //   var newData = {
    //     name: "Kim Hyun",
    //     userImage: "https://placeimg.com/100/100/arch",
    //     postImage: state.imageUrl,
    //     likes: 36,
    //     date: "May 15",
    //     liked: false,
    //     content: state.postContent,
    //     filter: state.choosenFilter,
    //   };
    //   state.instaData.unshift(newData);
    //   state.step = 0;
    // },
  },
  // actions: {
  //   getData(context) {
  //     axios.get(`https://codingapple1.github.io/vue/more${context.state.count}.json`).then((a) => {
  //       context.state.instaData.push(a.data)
  //       context.state.count++
  //     })
  //   }
  // }
})

export default store;