<template>
  <div>
    <div v-if="step == 0">
      <Post :instaData="instaData[i]" v-for="(a, i) in instaData" :key="i" />
    </div>

    <div v-if="step == 1">
      <div
        :class="choosenFilter + ' upload-image'"
        :style="{ backgroundImage: `url(${imageUrl})` }"
      ></div>
      <div class="filters">
        <FilterBox
          :filter="filter"
          :imageUrl="imageUrl"
          v-for="filter in filters"
          :key="filter"
        >
          <template v-slot:a>
            <span>{{ filter }}</span>
          </template>
        </FilterBox>
      </div>
    </div>

    <div v-if="step == 2">
      <div
        :class="choosenFilter + ' upload-image'"
        :style="{ backgroundImage: `url(${imageUrl})` }"
      ></div>
      <div class="wirte">
        <textarea
          @input="$emit('write', $event.target.value)"
          class="write-box"
        >
        write!
        </textarea>
      </div>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";
import FilterBox from "./FilterBox.vue";

export default {
  name: "Container",
  data() {
    return {
      filters: [
        "aden",
        "_1977",
        "brannan",
        "brooklyn",
        "clarendon",
        "earlybird",
        "gingham",
        "hudson",
        "inkwell",
        "kelvin",
        "lark",
        "lofi",
        "maven",
        "mayfair",
        "moon",
        "nashville",
        "perpetua",
        "reyes",
        "rise",
        "slumber",
        "stinson",
        "toaster",
        "valencia",
        "walden",
        "willow",
        "xpro2",
      ],
      choosenFilter: "",
    };
  },
  mounted() {
    this.emitter.on("boxClick", (a) => {
      this.choosenFilter = a;
    });
  },

  components: {
    Post,
    FilterBox,
  },
  props: {
    instaData: Array,
    step: Number,
    imageUrl: String,
  },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: 1px solid lightgray;
  width: 90%;
  height: 100px;
  margin: 10px auto 10px auto;
  display: block;
  outline: none;
}
</style>