<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step == 1 || step == 2" @click="step--">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">Î∞úÌñâ</li>
    </ul>
    <img src="./assets/logo2.png" class="logo" />
  </div>

  <Container
    :instaData="instaData"
    :step="step"
    :imageUrl="imageUrl"
    @write="postContent = $event"
  />
  <div class="seeMore">
    <button v-if="step == 0" @click="more">ÎçîÎ≥¥Í∏∞</button>
  </div>

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label v-if="step == 0" for="file" class="input-plus">
        <i id="white" class="far fa-plus-square"></i>
      </label>
    </ul>
  </div>

  <div stlye="margin-top : 500px;"></div>
</template>

<script>
import Container from "./components/Container.vue";
import Data from "./assets/data.js";
import { mapMutations } from "vuex";
// import { mapState, mapMutations, mapActions } from "vuex";
import axios from "axios";
axios.get();

export default {
  name: "App",
  data() {
    return {
      count: 0,
      step: 0,
      imageUrl: "",
      postContent: "",
      choosenFilter: "",
      instaData: Data,
    };
  },
  mounted() {
    this.emitter.on("boxClick", (a) => {
      this.choosenFilter = a;
    });
  },

  computed: {
    // ...mapState(["instaData", "step"]),
    ...mapMutations(["like"]),
    // ...mapActions(["getData"]),
  },

  components: {
    Container,
  },
  methods: {
    publish() {
      var newData = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.imageUrl,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.postContent,
        filter: this.choosenFilter,
      };
      this.instaData.unshift(newData);
      this.step = 0;
    },
    upload(e) {
      let imageFile = e.target.files;
      let url = URL.createObjectURL(imageFile[0]);
      this.imageUrl = url;
      this.step++;
    },
    more() {
      axios
        .get(`https://codingapple1.github.io/vue/more${this.count}.json`)
        .then((a) => {
          this.instaData.push(a.data);
          this.count++;
        });
    },
  },
};
</script>

