<template>
  <div class="post">
    <div class="post-header">
      <div
        class="profile"
        :style="{ backgroundImage: `url( ${instaData.userImage} )` }"
      ></div>
      <span class="profile-name">{{ instaData.name }}</span>
    </div>
    <div
      @click="$store.commit('like')"
      :class="instaData.filter + ' post-body'"
      :style="{ backgroundImage: `url(${instaData.postImage})` }"
    ></div>
    <div class="post-content">
      <p>
        <i @click="$store.commit('like')" class="far fa-heart"></i>
        {{ $store.state.likes }} Likes
      </p>
      <p>
        <strong>{{ instaData.filter }}</strong> {{ instaData.content }}
      </p>
      <p class="date">{{ instaData.date }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Post",
  props: {
    instaData: Object,
  },
};
</script>

<style>
.post {
  width: 100%;
}
.profile {
  width: 30px;
  height: 30px;
  background-size: 100%;
  border-radius: 50%;
  float: left;
}
.profile-name {
  display: block;
  float: left;
  padding-left: 10px;
  padding-top: 7px;
  font-size: 14px;
}
.post-header {
  height: 30px;
  padding: 10px;
}
.post-body {
  height: 450px;
  background-position: center;
  background-size: cover;
}
.post-content {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
}
.date {
  font-size: 11px;
  color: grey;
  margin-top: -8px;
}
</style>